import {useState} from 'react';

const useScroll = (ref) => {
	const [scroll, setScroll] = useState(0);

	const scrollHandler = () => {
		const {current} = ref;
		const percent = Math.floor(
			(current.scrollTop * 100) / (current.scrollHeight - current.clientHeight)
		);
		setScroll(percent);
	};

	return [scroll, scrollHandler];
};

export default useScroll;
