import React from 'react';

const Contact = (props) => {
	const {animation} = props;

	const titleClassAnimation =
		animation === 'open'
			? 'animate__jackInTheBox'
			: animation === 'close'
			? 'animate__zoomOut'
			: '';
	const socialClassAnimation =
		animation === 'open' ? 'animate__zoomIn' : animation === 'close' ? 'animate__zoomOut' : '';
	const {title} = props.data;
	return (
		<section className="contact container">
			<div className="contact__wrapper">
				<h2
					className={`title title--contact title--main contact__title animate__animated ${titleClassAnimation}`}>
					{title}
				</h2>
				<ul className="info contact__info">
					<li className={`info__item animate__animated animate__delay-16 ${socialClassAnimation}`}>
						<span className="info__def">tel:</span>
						<span className="info__data">+48 576 048 669</span>
					</li>
					<li className={`info__item animate__animated animate__delay-17 ${socialClassAnimation}`}>
						<span className="info__def">e-mail:</span>
						<span className="info__data">e.arechwa@gmail.com</span>
					</li>
				</ul>
			</div>
			<ul className="social contact__social">
				<li className={`social__item animate__animated animate__delay-13 ${socialClassAnimation}`}>
					<a href="https://github.com/sezardino" target="blank" className="social__link">
						git
					</a>
				</li>
				<li className={`social__item animate__animated animate__delay-14 ${socialClassAnimation}`}>
					<a
						href="https://www.facebook.com/edvard.arekhva/"
						target="blank"
						className="social__link">
						f
					</a>
				</li>
				<li className={`social__item animate__animated animate__delay-15 ${socialClassAnimation}`}>
					<a href="https://vk.com/sezardino" target="blank" className="social__link">
						vk
					</a>
				</li>
			</ul>
		</section>
	);
};

export default Contact;
