import React from 'react';

const About = (props) => {
	const {animation} = props;
	const animationClass =
		animation === 'open' ? 'animate__rollIn' : animation === 'close' ? 'animate__rollOut' : '';
	const {title, text, textEnd} = props.data;
	return (
		<section className="about container">
			<h2 className="title title--main title--about about__title">
				{title.map((item, index) => (
					<span
						key={item + index}
						className={`row animate__animated ${animationClass} animate__delay-${index}`}>
						{' '}
						{item.map((item, index) => (
							<span className="title__letter title__letter--scale" key={item + index}>
								{item}
							</span>
						))}
					</span>
				))}
			</h2>
			<div className="about__wrapper">
				<p
					className={`text text--about about__text animate__animated ${animationClass} animate__delay-2`}>
					{text}
				</p>
				<br />
				<p
					className={`text text--about about__text animate__animated ${animationClass} animate__delay-2`}>
					{textEnd}
				</p>
			</div>
		</section>
	);
};

export default About;
