import React from 'react';
import {observer} from 'mobx-react';
import {Link} from 'react-router-dom';
import {LANGUAGES, ROUTES} from '../../services';

import {LanguagesStore, PagesStore, MenuStore} from '../../store';

const Header = (props) => {
	const {data, activePage} = props;
	const {languageBar, activeLanguage, handler: languageBarHandler} = LanguagesStore;
	const {changePage} = PagesStore;
	const {menuOpen, toggleMenu} = MenuStore;
	const linkHandler = (path) => {
		changePage(path);
		toggleMenu();
	};

	return (
		<header className="header container">
			<button className="button button--hamburger header__button" onClick={toggleMenu}></button>
			<Link to={ROUTES.HOME} className="logo">
				<img src="/assets/img/logo.svg" alt="logo" />
			</Link>
			<div className={`menu header__menu ${menuOpen && `menu--active`}`}>
				<button className="button button--close menu__button" onClick={toggleMenu}></button>
				<nav className="nav nav--header menu__nav header__nav">
					{data.map((item, index) => {
						const isCurrent = activePage === item.path ? 'nav__link--active' : '';
						return (
							<button
								// to={item.path}
								className={`nav__link ${isCurrent}`}
								key={item.name + index}
								onClick={() => linkHandler(item.path)}>
								{item.name}
							</button>
						);
					})}
				</nav>
				<ul
					className={`languages ${
						languageBar && 'languages--active'
					} menu__languages header__languages`}>
					{Object.values(LANGUAGES).map((item, index) => {
						const isActive = item === activeLanguage ? 'languages__button--active' : '';
						return (
							<li className="languages__item" key={item + index}>
								<button
									className={`languages__button ${isActive}`}
									onClick={() => languageBarHandler(item)}>
									{item}
								</button>
							</li>
						);
					})}
					{/* <li className="languages__item">
						<button className="languages__button languages__button--active">en</button>
					</li>
					<li className="languages__item">
						<button className="languages__button">ru</button>
					</li>
					<li className="languages__item">
						<button className="languages__button">pl</button>
					</li> */}
				</ul>
			</div>
		</header>
	);
};

export default observer(Header);
