import React, {useEffect} from 'react';
import {Switch, Route} from 'react-router';
import {observer} from 'mobx-react';
import {useHistory} from 'react-router-dom';
import {ROUTES, swipe} from '../../services';
import {LanguagesStore, PagesStore} from '../../store';

import {DATA} from '../../services';

import Footer from '../footer';
import Header from '../header';

import {Home, About, Contact, Work, Portfolio, Skills} from '../pages';

const App = () => {
	const history = useHistory();
	const language = LanguagesStore.activeLanguage;
	const {skills, works} = DATA;
	const {PAGES, HERO, ABOUT, WORKS, CONTACT, SKILLS, POPUP} = DATA[language];
	const {activePage, animation, changePage, setPage} = PagesStore;
	const mainRef = React.createRef();
	useEffect(() => {
		setPage(history.location.pathname);
	}, []);

	useEffect(() => {
		history.push(activePage);
	}, [activePage]);

	useEffect(() => {
		document.addEventListener('touchstart', (evt) => swipe(evt, changePage));

		return () => {
			document.addEventListener('touchstart', (evt) => swipe(evt, changePage));
		};
	}, []);

	return (
		<React.Fragment>
			<Header data={PAGES} activePage={activePage} history={history} />
			<main ref={mainRef}>
				<h1 className="hidden"> Edward Arechwa Junior Front - end Developer </h1>{' '}
				<Switch>
					<Route path="/" exact render={() => <Home animation={animation} data={HERO} />} />
					<Route path="/about" render={() => <About animation={animation} data={ABOUT} />} />
					<Route
						path={ROUTES.SKILLS}
						render={() => <Skills animation={animation} data={{skills, ...SKILLS}} />}
					/>
					<Route
						path={ROUTES.WORKS}
						exact
						render={() => <Portfolio animation={animation} data={{works, ...WORKS}} />}
					/>
					<Route
						path={ROUTES.CONTACT}
						render={() => <Contact animation={animation} data={CONTACT} />}
					/>
					<Route
						path="/works/:id"
						render={({
							match: {
								params: {id},
							},
						}) => <Work data={{work: works[id], ...POPUP}} />}
					/>
				</Switch>
			</main>
			<Footer data={PAGES} activePage={activePage} />
		</React.Fragment>
	);
};

export default observer(App);
