import React from 'react';
import {useScroll} from '../../hooks';
import {observer} from 'mobx-react';
import {useHistory} from 'react-router-dom';
import {PopupStore} from '../../store';

const Work = (props) => {
	const imgRef = React.createRef();

	const [scroll, scrollHandler] = useScroll(imgRef);
	const {mobileMockup, desktopMockup, mobileHandler, desktopHandler} = PopupStore;
	const {
		technologiesTitle,
		GHPagesLink,
		gitHubLink,
		work: {name, mockup, mobile, desktop, ghPages, github, technologies},
	} = props.data;
	const history = useHistory();
	let img;
	if (!mobile && !desktop) {
		img = mockup;
	} else if (mobileMockup) {
		img = mobile;
	} else if (desktopMockup) {
		img = desktop;
	}
	const mockupClass = desktopMockup ? 'view__mockup--desktop' : 'view__mockup--mobile';

	const closeHandler = () => {
		history.goBack();
		desktopHandler();
	};
	return (
		<section className="popup">
			<div className=" popup__wrapper">
				<button
					className="button button-close button-close--dark popup__button"
					onClick={closeHandler}></button>
				<h2 className="title title--main title--popup popup__title">{name}</h2>
				<div className="view popup__view">
					<div className="view__mockup-wrapper">
						<div className={`view__mockup ${mockupClass}`}>
							<p className="view__img" ref={imgRef} onScroll={scrollHandler}>
								<img src={img} alt={name} />
							</p>
						</div>
						<div className="scroll scroll--black view__scroll">
							<div className="scroll__track">
								<div
									className="scroll__bar"
									style={{top: `${scroll}%`, transform: `translate(-50%,-${scroll}%)`}}></div>
							</div>
						</div>
					</div>
					<div className="view__buttons">
						<button
							className={`button button-view view__button ${
								desktopMockup && 'button-view--active'
							}`}
							onClick={desktopHandler}>
							<div className="button__image">
								<img src="/assets/img/icons/desktop.png" alt="desktop icon" />
							</div>
							<span className="button-view__text">Desktop</span>
						</button>
						{mobile && (
							<button
								className={`button button-view view__button ${
									mobileMockup && 'button-view--active'
								}`}
								onClick={mobileHandler}>
								<div className="button__image">
									<img src="/assets/img/icons/mobile.png" alt="mobile icon" />
								</div>
								<span className="button-view__text">Mobile</span>
							</button>
						)}
					</div>
				</div>
				<div className="description popup__description">
					<h2 className="title title--main title--popup description__title">{name}</h2>
					<div className="description__buttons">
						<a href={github} target="blank" className="button button--popup description__button">
							{gitHubLink}
						</a>
						<a href={ghPages} target="blank" className="button button--popup description__button">
							{GHPagesLink}
						</a>
					</div>
					<div className="technologies description__technologies">
						<h3 className="title title--technologies technologies__title">{technologiesTitle}</h3>
						<ul className="technologies__list">
							{technologies.map((item, index) => (
								<li className="technologies__item" key={item + index}>
									{item}
								</li>
							))}
						</ul>
					</div>
				</div>
			</div>
		</section>
	);
};

export default observer(Work);
