import React from 'react';
import {Link} from 'react-router-dom';
import {useScroll} from '../../hooks';

const Portfolio = (props) => {
	const {animation} = props;

	const portfolioRef = React.createRef();
	const [scroll, scrollHandler] = useScroll(portfolioRef);
	const titleClassAnimation =
		animation === 'open'
			? 'animate__rotateInDownRight'
			: animation === 'close'
			? 'animate__rotateOutDownRight'
			: '';
	const workClassAnimation = animation === 'open' ? 'animate__flipInX' : '';
	const worksClassAnimation = animation === 'close' ? 'animate__flipOutX' : '';
	const scrollClassAnimation =
		animation === 'open'
			? 'animate__fadeIn animate__delay-5'
			: animation === 'close'
			? 'animate__fadeOut animate__delay-13'
			: '';
	const {title, works} = props.data;
	return (
		<section className="portfolio container">
			<h2 className="title title--main title--portfolio portfolio__title">
				{title.map((item, index) => (
					<span
						className={`row animate__animated ${titleClassAnimation} animate__delay-1${index}`}
						key={item + index}>
						{item.map((item, index) => (
							<span className="title__letter title__letter--scale" key={item + index}>
								{item}
							</span>
						))}
					</span>
				))}
			</h2>
			<div className="portfolio__wrapper ">
				<ul
					className={`portfolio__list animate__animated ${worksClassAnimation} animate__delay-1`}
					ref={portfolioRef}
					onScroll={scrollHandler}>
					{works.map((item, index) => {
						const {name, mockup} = item;
						return (
							<li
								className={`portfolio__item animate__animated ${workClassAnimation} animate__delay-1${
									index + 4
								}`}
								key={item + index}>
								<Link to={`/works/${index}`}>
									<img src={mockup} alt={name} />
								</Link>
							</li>
						);
					})}
				</ul>
				<div
					className={`scroll scroll--white portfolio__scroll animate__animated ${scrollClassAnimation}`}>
					<div className="scroll__track">
						<div
							className="scroll__bar"
							style={{top: `${scroll}%`, transform: `translate(-50%,-${scroll}%)`}}></div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default Portfolio;
