import {observable, action, makeObservable} from 'mobx';
import {ROUTES, selectPage} from '../services';

class Pages {
	constructor() {
		makeObservable(this, {
			activePage: observable,
			animation: observable,
			nextPage: observable,
			prevPage: observable,
			changePage: action,
			setPage: action,
			switching: action,
		});
	}

	activePage = ROUTES.HOME;
	animation = 'open';
	nextPage = ROUTES.ABOUT;
	prevPage = ROUTES.CONTACT;

	setPage = (page) => {
		this.activePage = page;
		this.nextPage = selectPage(1, this.activePage);
		this.prevPage = selectPage(-1, this.activePage);
	};

	switching = (page) => {
		if (page === 1) {
			this.activePage = selectPage(1, this.activePage);
		} else if (page === -1) {
			this.activePage = selectPage(-1, this.activePage);
		} else {
			this.activePage = page;
		}
		this.nextPage = selectPage(1, this.activePage);
		this.prevPage = selectPage(-1, this.activePage);
		this.animation = 'open';
	};

	changePage = (page) => {
		this.animation = 'close';
		setTimeout(() => this.switching(page), 1500);
	};
}

const PagesStore = new Pages();

export default PagesStore;
