import React from 'react';
import {PagesStore} from '../../store';

import {observer} from 'mobx-react';

const Footer = (props) => {
	const {data, activePage} = props;
	const {changePage} = PagesStore;
	return (
		<footer className="footer container">
			<button
				className="button button-arrow button-arrow--prev footer__prev"
				onClick={() => changePage(-1)}>
				<span className="hidden">Prev page</span>
			</button>
			<nav className="nav nav--footer footer__nav">
				{data.map((item, index) => {
					const isCurrent = activePage === item.path ? 'nav__link--active' : '';
					return <p key={item + index} className={`nav__link ${isCurrent}`}></p>;
				})}
			</nav>
			<button
				className="button button-arrow button-arrow--next footer__next"
				onClick={() => changePage(1)}>
				<span className="hidden">Next page</span>
			</button>
		</footer>
	);
};

export default observer(Footer);
