import {observable, action, makeObservable} from 'mobx';

class Menu {
	constructor() {
		makeObservable(this, {
			menuOpen: observable,
			toggleMenu: action,
		});
	}

	menuOpen = false;

	toggleMenu = () => {
		this.menuOpen = !this.menuOpen;
	};
}

const MenuStore = new Menu();

export default MenuStore;
