import {ROUTES} from './index';

const selectPage = (n, active) => {
	const routes = Object.values(ROUTES);
	if (n === -1) {
		let index = routes.findIndex((item) => item === active);
		index = index - 1 < 0 ? routes.length - 1 : index - 1;
		return routes[index];
	} else {
		let index = routes.findIndex((item) => item === active);
		index = index + 1 > routes.length - 1 ? 0 : index + 1;
		return routes[index];
	}
};

const touch = {
	event: false,
	startX: 0,
	dist: 0,
	shiftX: 0,
	startTime: 0,
	time: 0,
	minDist: 60,
	maxDist: 400,
	maxTime: 700,
	minTime: 150,
};
const swipe = (evt, action) => {
	touch.startX = Math.floor(evt.changedTouches[0].clientX);
	touch.startTime = new Date().getTime();
	if (!touch.event) {
		touch.event = true;
		const touchendHandler = (evt) => {
			const endTime = new Date().getTime();
			touch.time = endTime - touch.startTime;
			touch.shiftX = Math.floor(evt.changedTouches[0].clientX) - touch.startX;
			touch.dist = Math.abs(touch.shiftX);
			if (
				touch.dist < touch.minDist ||
				touch.dist > touch.maxDist ||
				touch.time < touch.minTime ||
				touch.time > touch.maxTime
			) {
				return;
			}
			if (
				touch.dist > touch.minDist ||
				touch.dist < touch.maxDist ||
				touch.time > touch.minTime ||
				touch.time < touch.maxTime
			) {
				if (touch.shiftX < 0) {
					action(1);
				}
				if (touch.shiftX > 0) {
					action(-1);
				}
			}
		};
		document.addEventListener('touchend', (evt) => {
			touchendHandler(evt);
		});
		document.removeEventListener('touchend', (evt) => {
			touchendHandler(evt);
		});
	}
};

export {selectPage, swipe};
