import React from 'react';
import {LEVEL} from '../../services';

const Skills = (props) => {
	const {title, skills} = props.data;
	const {animation} = props;

	const titleClassAnimation =
		animation === 'open'
			? 'animate__fadeInRight'
			: animation === 'close'
			? 'animate__fadeOutRight'
			: '';
	const skillClassAnimation =
		animation === 'open'
			? 'animate__fadeInLeft'
			: animation === 'close'
			? 'animate__fadeOutLeft'
			: '';
	return (
		<section className="skills container">
			<h2 className="title title--main title--skills skills__title">
				{title.map((item, index) => (
					<span
						className={`row animate__animated ${titleClassAnimation} animate__delay-${index}`}
						key={item + index}>
						{' '}
						{item.map((item, index) => (
							<span className="title__letter title__letter--scale" key={item + index}>
								{item}
							</span>
						))}
					</span>
				))}
			</h2>
			<ul className="skills-list skills__skills-list">
				{skills.map((item, index) => {
					const {name, level} = item;
					const skillAnimationDelay = index % 2 === 0 ? '' : 'animate__delay-1';
					const dotsAnimationDelay = index % 2 === 0 ? 'animate__delay-2' : 'animate__delay-2';
					return (
						<li
							className={`skills-list__item animate__animated ${skillClassAnimation} ${skillAnimationDelay}`}
							key={name + level + index}>
							<p className="skills-list__skill">{name}</p>
							<ul
								className={`dots skills-list__dots animate__animated ${skillClassAnimation} ${dotsAnimationDelay}`}>
								{LEVEL.map((item, index) => {
									const active = index < level ? 'dots__item--active' : '';
									return (
										<li className={`dots__item ${active}`} key={item + index}>
											<p className="hidden">item</p>
										</li>
									);
								})}
							</ul>
						</li>
					);
				})}
			</ul>
		</section>
	);
};

export default Skills;

const item = (
	<li className="skills-list__item">
		<p className="skills-list__skill">HTML</p>
		<ul className="dots skills-list__dots">
			<li className="dots__item dots__item--active">
				<p className="hidden">understand</p>
			</li>
			<li className="dots__item dots__item--active">
				<p className="hidden">junior</p>
			</li>
			<li className="dots__item dots__item--active">
				<p className="hidden">regular</p>
			</li>
			<li className="dots__item">
				<p className="hidden">advanced</p>
			</li>
			<li className="dots__item">
				<p className="hidden">master</p>
			</li>
		</ul>
	</li>
);
