import React from 'react';

// import img from 'assets/img/hero.png';
// console.log(img);

const Home = (props) => {
	const {animation} = props;
	const titleClassAnimation =
		animation === 'open'
			? 'animate__fadeInBottomLeft'
			: animation === 'close'
			? 'animate__fadeOutBottomLeft'
			: '';
	const imgClassAnimation =
		animation === 'open'
			? 'animate__fadeInBottomRight'
			: animation === 'close'
			? 'animate__fadeOutBottomRight'
			: '';

	return (
		<section className="hero container">
			<p className={`hero__img animate__animated ${imgClassAnimation}`}>
				<picture>
					<source media="(min-width: 768px)" srcSet="assets/img/hero_desktop.png" />
					<img src="assets/img/hero.png" alt="" />
				</picture>
			</p>
			<div className="hero__wrapper">
				<h2 className="title title--hero hero__title">
					<span
						className={`title title--name animate__animated animate__delay-12 ${titleClassAnimation}`}>
						Edward
					</span>
					<span
						className={`title title--surname animate__animated animate__delay-13 ${titleClassAnimation}`}>
						Arechwa
					</span>
				</h2>
				<p
					className={`text text--hero hero__text animate__animated animate__delay-14 ${titleClassAnimation}`}>
					junior front-end developer
				</p>
			</div>
		</section>
	);
};

export default Home;
