import {observable, action, makeObservable} from 'mobx';
import {LANGUAGES} from '../services/const';

class LanguageBar {
	constructor() {
		makeObservable(this, {
			activeLanguage: observable,
			languageBar: observable,
			handler: action,
		});
	}

	activeLanguage = LANGUAGES.EN;
	languageBar = false;

	handler = (language) => {
		if (!this.languageBar) {
			this.languageBar = true;
		} else if (this.languageBar) {
			this.activeLanguage = language;
			this.languageBar = false;
		}
	};
}

const LanguagesStore = new LanguageBar();

export default LanguagesStore;
