import {observable, action, makeObservable} from 'mobx';

class Popup {
	constructor() {
		makeObservable(this, {
			mobileMockup: observable,
			desktopMockup: observable,
			desktopHandler: action,
			mobileHandler: action,
		});
	}
	desktopMockup = true;
	mobileMockup = false;

	desktopHandler = () => {
		this.mobileMockup = false;
		this.desktopMockup = true;
	};

	mobileHandler = () => {
		this.mobileMockup = true;
		this.desktopMockup = false;
	};
}

const PopupStore = new Popup();

export default PopupStore;
